<template>
    <div class="current">
        <div class="contain">
            <div class="daohang" @mouseenter="appearNav"></div>
            <headernav v-if="appear" class="header-current"></headernav>   
            <video id="video" class="play-v" poster="../assets/current-bgi.png" controls>
                <source src="../../static/video/current.mp4"> 
            </video>
            <button id="btn" class="control" @click="Toplay"></button>
            <div class="passage">
                <div class="left">
                    <div class="decline">
                        <h4 class="title-d"></h4>
                        <p class="text-d"> 
                            
                            
                            </p>
                    </div>
                    <div class="protect-logo">
                    <img src="../assets/protect-logo.png" alt>
                    </div>
                </div>
                <div class="way">
                    <h3 class="title-w"><br/></h3>
                    <div class="text-w">
                        <p>
                            
                            </p>
                        <p>2005</p>   
                        <div>
                            <img src="../assets/ngx.png" alt width="100%"> 
                            <p class="pic_pro">""</p>
                        </div>
                        <p class="Clear">2005</p>
                        <p>2009 </p> 
                        <p>
                             
                            </p>
                        <div class="pic_sort">
                            <img class="img" src="../assets/cu1.png" alt>
                            <img class="img" src="../assets/cu2.png" alt>
                            <img class="img" src="../assets/cu1.png" alt>
                            <img class="img" src="../assets/cu2.png" alt>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>   
        <div class="bottom">
            <div class="b_top">
                <div class="LinkToUs">
                    <p class="bottom_title"></p>
                    <div class="Link">
                        <div class="border pic1"></div>
                        <div class="border pic2"></div>
                        <div class="border pic3"></div>
                        <div class="border pic4"></div>
                        <div class="border pic5"></div>
                    </div>
                </div>
                <div class="share">
                    <p class="bottom_title"></p>
                    <div class="border pic6"></div>
                </div>
                <div class="master">
                    <p class="bottom_title"></p>
                    <p class="m_person">  </p>
                    <p class="m_person">  </p>
                </div>
            </div>
            <div class="b_bottom">
                <p class="feedback">
                    <span class="question"></span>
                    <span class="report"></span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import HeaderNav from './HeaderNav'
export default {
    name: "Current",
    data(){
        return{
            appear: false,
        }
    },
    components:{
        "headernav": HeaderNav
    },
    methods:{
        appearNav(){
            this.appear = ! this.appear
        },
        Toplay(){
            var video = document.getElementById("video");
            var btn = document.getElementById("btn");
            video.play();
            btn.style.display = "none"
        }
    },
    mounted(){       
        var video = document.getElementById("video");
        var btn = document.getElementById("btn");
        video.addEventListener('pause',function(){
            btn.style.display = "block"
        })
        video.addEventListener('play',function(){
            btn.style.display = "none"
        })
    }
}
</script>

<style scoped>
.daohang{
    background: url("../assets/daohang.png") no-repeat;
    background-position-x: center;
    background-size: 70%;
    width: 4%;
    height: 2%;
    position: absolute;
    z-index: 100;
    left: 50%;
    cursor: pointer;
}
.header-current{
    /* background-color: #503C2A; */
    /* opacity: 0.6; */
    background: url("../assets/header4.png") no-repeat;
    background-size: cover;
    position: absolute;
    z-index: 99;
    left: 0;
    top: 0;
    width: 100%;
}
.contain{
    background-image: url("../assets/currentBgi.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.control{
    background: url("../assets/bofang.png") no-repeat;
    background-size: 40%;
    background-position: center;
    width: 10vh;
    height: 10vh;
    background-color: rgba(0,0,0,1);
    opacity: 0.6;
    position: absolute;
    left: 50%;
    top: 10%;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    z-index: 99;
    outline: none;
}
.play-v{
    width: 100%;
    outline: none;
    position: relative;
}
.passage{
    color: #3B3A3A;
    width: 100%;
    display: flex
}
.left{
    width: 50%;
}
.decline{
    margin-left: 25%;
    margin-top: 10vh;
    width: 62%;
    /* background-color: yellow; */
}
.title-d{
    font-size: 2em;
    font-weight: 400;
    border-left: 2px solid #707070;
    padding: 10px;
}
p.text-d{
    font-size: 1.2em;
    width: 55%;
    padding: 13px 0 0 13px;
    letter-spacing: 0.1em;
}
.way{
    /* background-color: pink; */
    display: inline-block;
    width: 50%;
    margin-top: 35vh;
    padding-bottom: 20vh;
}
.protect-logo{
    width: 20%;
    clear: left;
    padding-top: 15vh;
    margin-left: 25%;
}
h3.title-w {
    border-left: 2px solid #707070;
    padding: 20px;
    font-size: 3em;
    font-weight: 300;
    margin-left: 35%;
    margin-top: 15vh;
}
.text-w{
    font-size: 1.2em;
    width: 70%;
    margin-top: 15vh;
    /* font-weight: 300; */
}
.text-w p{
    margin-bottom: 4vh;
}
p.pic_pro {
    font-size: 0.6em;
    float: right;
}
p.Clear {
    clear: both;
}
.pic_sort {
    display: flex;
    justify-content: space-between;
}
img.img {
    width: 20%;
}
.bottom {
    /* background-color: #D8CCC0; */
    background: url("../assets/current_header.png") no-repeat;
    background-size: cover;
    height: 30vh;
    color: rgb(112, 101, 95);
}
.b_top {
    width: 80%;
    margin: 0 auto;
    display: flex;
    border-bottom: 1px solid #C4BAAF;
    justify-content: space-between;
    padding-top: 5vh;
    padding-bottom: 5vh;
    font-size: 0.9em;
}
p.bottom_title {
    margin-bottom: 3vh;
}
.LinkToUs{
    width: 25%;
}
.master{
    width: 25%;
}
.share{
    width: 25%;
}
.border {
    height: 5vh;
    width: 5vh;
    border-radius: 50%;
    margin-right: 5%;
    opacity: 0.6;
}
p.m_person {
    font-size: 0.8em;
}
p.feedback {
    margin: 0 auto;
    width: 80%;
    font-size: 0.9em;
    padding-top: 3vh;
}
span.report {
    float: right;
}
.Link{
    display: flex;
}
.pic1{
    background: url("../assets/youjian1.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
.pic2{
    background: url("../assets/qq.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
.pic3{
    background: url("../assets/weixin.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
.pic4{
    background: url("../assets/weibo.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
.pic5{
    background: url("../assets/dianhua.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
.pic6{
    background: url("../assets/link-fill.png") no-repeat;
    background-position: center;
    background-size: 50%;
    background-color: rgb(194, 181, 173);
}
</style>




