<template>
    <div class="quanjin">
        <div id="container"></div>
    </div>
</template>

<script>
import PhotoSphereViewer from 'photo-sphere-viewer'
import 'photo-sphere-viewer/dist/photo-sphere-viewer.css'
import axios from 'axios'
export default {
    mounted(){
        //
        // window.onload=function(){
        //     getTitleHeight();
        //     loadingAllImg();
        // }
        getTitleHeight();
        loadingAllImg();

        //
        var canvasHeight;
        function getTitleHeight(){
            // var title=document.getElementById('title');
            // var titleHeight=parseFloat(getComputedStyle(title).height);
            var maxHeight=window.innerHeight;
            // canvasHeight=parseFloat(maxHeight-titleHeight)+'px';
            canvasHeight=parseFloat(maxHeight)+'px';
        }
        //
        function loadingAllImg(){
            var div = document.getElementById('container');
            var PSV = new PhotoSphereViewer({
                // 
                panorama: 'http://47.101.214.230:8080/showpic/9',
                // 
                container: div,
                // 2000time_animfalse
                time_anim: false,
                // false
                navbar: true,
                // null{width: 500, height: 300}
                size: {
                    width: '100%',
                    height: canvasHeight
                }
            });
        }
    }
}
</script>

<style scoped>
.quanjin{
    /* max-width: 640px; */
    width: 100%;
    margin: 0 auto;
}
</style>


